h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.header {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  flex-direction: column;
  column-gap: 20px;
  margin: 0 0 70px;
  display: grid;
}

@media (width >= 640px) {
  .header {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .header {
    --columns: 12;
  }
}

.header__image {
  background-image: url("header-img.58ec98f0.png");
  background-position: center;
  background-size: cover;
  grid-column: 1 / -1;
  justify-content: center;
  width: 100%;
  height: 320px;
  display: flex;
}

@media (width >= 320px) {
  .header__image {
    justify-content: center;
    width: 100%;
    max-width: 100%;
    height: 320px;
  }
}

@media (width >= 640px) {
  .header__image {
    order: 2;
    grid-column: 4 / -1;
    justify-content: left;
    height: 384px;
  }
}

@media (width >= 1280px) {
  .header__image {
    grid-column: 6 / -1;
    height: 655px;
  }
}

.header__logo {
  visibility: visible;
  width: 55px;
  height: 61px;
  margin-top: 9px;
  transition: scale .3s;
}

.header__logo:hover {
  scale: 1.01;
}

@media (width >= 640px) {
  .header__logo {
    grid-column: 4 / 5;
    margin: 15px 90px;
  }
}

@media (width >= 1280px) {
  .header__logo {
    width: 70px;
    height: 70px;
    margin: 15px 50px;
  }
}

.header__logo-link {
  visibility: hidden;
}

.header__description {
  grid-column: 1 / -1;
  padding-inline: 20px;
}

@media (width >= 640px) {
  .header__description {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .header__description {
    margin-inline: auto;
    padding-inline: 55px;
  }
}

@media (width >= 640px) {
  .header__description {
    order: 1;
    grid-column: 1 / 4;
    margin: 127px 0 0;
    padding-right: 0;
  }
}

@media (width >= 1280px) {
  .header__description {
    grid-column: 1 / 6;
    height: 655px;
    margin: 206px 0 0;
  }
}

.header__description--title {
  text-align: center;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  font-weight: 700;
  line-height: 45px;
}

@media (width >= 640px) {
  .header__description--title {
    text-align: left;
    margin: 0 0 38px;
    font-family: Montserrat, sans-serif;
    font-size: 54px;
    font-weight: 700;
    line-height: 64.8px;
  }
}

@media (width >= 1280px) {
  .header__description--title {
    font-size: 72px;
    line-height: 86.4px;
  }
}

.header__description__buttons {
  display: block;
}

@media (width >= 640px) {
  .header__description__buttons {
    display: flex;
  }
}

@media (width >= 1280px) {
  .header__description__buttons {
    height: 200px;
    display: flex;
  }
}

.header__description--tickets {
  background-color: #0000;
  border: 1px solid #1a5a4c;
  justify-content: space-between;
  transition: scale .3s;
  display: flex;
}

.header__description--tickets:hover {
  scale: 1.01;
}

@media (width >= 640px) {
  .header__description--tickets {
    width: 270px;
    height: 70px;
  }
}

@media (width >= 1280px) {
  .header__description--tickets {
    display: flex;
  }
}

.header__description--tickets--text {
  text-transform: uppercase;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  position: relative;
  left: 40%;
}

@media (width >= 640px) {
  .header__description--tickets--text {
    margin: 27px 0;
    left: 30%;
  }
}

.header__description--tickets--button {
  background-color: #1a5a4c;
  width: 50px;
  height: 50px;
  transition: background-color .3s;
}

.header__description--tickets--button:hover {
  background-color: #353535;
}

@media (width >= 640px) {
  .header__description--tickets--button {
    width: 70px;
    height: 70px;
  }
}

.header__description--events {
  display: none;
}

@media (width >= 1280px) {
  .header__description--events {
    text-transform: uppercase;
    text-align: left;
    color: #687480;
    font-family: IBM Plex Sans, serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 14px;
    display: inline-block;
    position: relative;
  }
}

.header__description--events:before {
  content: "";
  background-color: #1a5a4c;
  width: 70px;
  height: 1px;
  margin: 7px 20px 0 0;
  display: block;
  position: absolute;
  left: -80px;
}

@media (width >= 1280px) {
  .header__description--events-link {
    text-align: center;
    transform-origin: 0;
    margin: 0 auto;
    text-decoration: none;
    display: block;
    transform: rotate(270deg)translateX(-10px);
  }
}

.top-bar {
  padding-inline: 20px;
  display: flex;
  position: absolute;
}

@media (width >= 640px) {
  .top-bar {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .top-bar {
    margin-inline: auto;
    padding-inline: 55px;
  }
}

.top-bar__icons {
  display: flex;
}

@media (width >= 640px) {
  .top-bar {
    order: 0;
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .top-bar {
    padding-inline: 55px;
  }
}

.icon {
  background-size: cover;
  width: 24px;
  height: 24px;
  margin-top: 27px;
  transition: scale .3s;
  display: block;
}

.icon:hover {
  scale: 1.01;
}

@media (width >= 640px) {
  .icon {
    margin-top: 39px;
  }
}

.icon--menu {
  background-image: url("icon-menu.89cbb85a.png");
}

.icon--close {
  background-image: url("icon-close.a74f581f.png");
}

.icon--facebook {
  background-image: url("icon-facebook.699dfb6e.png");
}

.icon--instagram {
  background-image: url("icon-instagram.94b3b989.png");
}

.icon--swipe {
  margin: 25%;
}

@media (width >= 640px) {
  .icon--swipe {
    margin: 35%;
  }
}

.icon--swipe--sub {
  margin: 25%;
}

@media (width >= 640px) {
