}

.menu {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  background-color: #1a5a4c;
  flex-direction: column;
  justify-content: space-between;
  column-gap: 20px;
  height: auto;
  display: grid;
}

@media (width >= 640px) {
