}

.menu__schedule--week {
  color: #fff;
  text-align: left;
  font-family: IBM Plex Sans, serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}

@media (width >= 640px) {
  .menu__schedule--week {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 23px;
    margin: 20px 0 0;
    display: grid;
  }
}

@media (width >= 1280px) {
  .menu__schedule--week {
    grid-template-columns: repeat(5, 1fr);
    column-gap: 30px;
  }
}

.menu__schedule--week-days {
  margin: 20px 0 16px;
}

@media (width >= 640px) {
  .menu__schedule--week-days {
    grid-column: span 1;
    margin: 0;
  }
}

@media (width >= 1280px) {
  .menu__schedule--week-days {
    grid-column: span 2;
  }
}

@media (width >= 640px) {
  .menu__schedule--holidays {
    grid-column: span 1;
  }
}

@media (width >= 1280px) {
  .menu__schedule--holidays {
    grid-column: span 2;
  }
}

.menu__schedule--holidays--text {
  margin: 0;
}

